.preloader {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: rgba(19,19,19,.8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &__block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center; }
    &__logo {
        text-align: center;
        & svg {
            animation: blink-logo 3s infinite both; } }

    &__text {
        text-align: center;
        margin-top: 16px;
        display: inline-block;
        margin-left: auto;
        margin-right: auto;
        clip-path: inset(0 3ch 0 0);
        animation: dots 1s steps(4) infinite; } }


@keyframes blink-logo {
    0%,
    50%,
    100% {
        opacity: 1; }
    25%,
    75% {
        opacity: 0; } }

@keyframes dots {
    to {
        clip-path: inset(0 -1ch 0 0); } }
